* {
  box-sizing: border-box;
}

html {
  margin: 0;
}

body {
  font-family: "League Spartan", sans-serif;
  background-color: rgb(248, 248, 251);
}

h1 {
  color: rgb(12, 14, 22);
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: -0.04688rem;
  margin: 0;
  margin-bottom: 0.19rem;
}

h3 {
  color: rgb(136, 142, 176);
  font-size: 0.8125rem;
  font-weight: 500;
  line-height: 0.9375rem; /* 115.385% */
  letter-spacing: -0.00625rem;
  margin: 0;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 2.25rem;
    font-weight: 700;
    letter-spacing: -0.07031rem;
    margin-bottom: 0.38rem;
  }
  h3 {
    font-size: 0.8125rem;
    font-style: normal;
    font-weight: 500;
    line-height: 0.9375rem; /* 115.385% */
    letter-spacing: -0.00625rem;
  }
}
button {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: 0;
}
button:hover {
  outline: none;
}
button:focus {
  outline: none;
}

.btn {
  border-radius: 24px;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 3rem;
  font-size: 0.9375rem;
  font-weight: 700;
  line-height: 0.9375rem; /* 100% */
  letter-spacing: -0.01563rem;
  padding-top: 1.06rem;
  padding-bottom: 1.06rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.btn-primary {
  background-color: rgb(124, 93, 250);
  color: #FFF;
  text-align: center;
  font-size: 0.9375rem;
  font-style: normal;
  font-weight: 700;
  line-height: 0.9375rem; /* 100% */
  letter-spacing: -0.01563rem;
}
.btn-primary:hover {
  background-color: rgb(146, 119, 255);
}

.btn-red {
  background-color: rgb(236, 87, 87);
  color: #FFF;
  font-size: 0.9375rem;
  font-weight: 700;
  line-height: 0.9375rem; /* 100% */
  letter-spacing: -0.01563rem;
}
.btn-red:hover {
  background-color: rgb(255, 151, 151);
}

.btn-transparent {
  background: rgba(0, 0, 0, 0.1);
