}

.btn-dark {
  background: #373B53;
  color: rgb(126, 136, 195);
}
.btn-dark:hover {
  background: rgb(20, 22, 37);
  color: rgb(126, 136, 195);
}

.errorMessage {
  font-size: 0.8rem;
  color: red;
}

.navigationBar {
  height: 4.5rem;
  padding: 0;
  background: #373B53;
  margin-bottom: 2.25rem;
